import { Box, Text } from "@chakra-ui/react";
import { Card } from '../../components';

const Servicios = () => {

// ---------estilos--------
  const estiloservicios = {
    bg: "lightcoral",
  };

// --------props---------
  const servicios = [
    {
      img: 'desarrollo-web',
      titulo: "Desarrollo Web",
      descripcion:
        "Me especializo en la creacin de sitios web, aplicaciones e interfaces de fcil interaccin para los visitantes y usuarios.",
    },
    {
      img: 'desarrollo-movil',
      titulo: "Desarrollo Movil",
      descripcion:
        "Diseo, desarrollo e implemento aplicaciones mviles.Adems soluciono problemas y depuro la aplicacin para optimizar su rendimiento.",
    },
    {
      img: 'desarrollo-web',
      titulo: "Diseo Responsive",
      descripcion:
        "Me encargo de buscar la correcta visualizacin de una misma pgina en distintos dispositivos, ya sea en PC de Escritorio, Tablets o Celulares",
    },
    {
      img: 'base-de-datos',
      titulo: "Base de datos",
      descripcion:
        "Otra de las funciones que empleo es la creacin de base de datos y servidores eficientes. Ademas domino el manejo de software de almacenamiento.",
    },
    {
      img: 'optimizacion',
      titulo: "Optimizacin del Sitio",
      descripcion:
        "Utilizando herramientas y estrategias, monitoreo y actualizo los sistemas para asi mejorar el rendimiento de la pagina.",
    },
    {
      img: 'gestin-de-proyectos',
      titulo: "Gestin de Proyectos",
      descripcion:
        "Poseo una mirada mas comprensiva y panormica de todo el proceso de creacin y monitoreo de aplicaciones y pginas web.",
    },
  ];

  return (
    <Box id="servicios" as="section" sx={estiloservicios}>
      <Text as="h2" fontSize={25} textAlign='center'>Servicios</Text>

      {servicios.map((serv) => (
        <Card
          titulo={serv.titulo}
          descripcion={serv.descripcion}
          img={serv.img}
        />
      ))}
    </Box>
  );
};

export { Servicios };